.root {
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  );

  box-shadow: 0px 0px 24px rgba(72, 53, 234, 0.196487) inset;
  border-radius: 8px;
}
.tab {
  width: 50%;
  min-width: 50px;
  background-color: transparent !important;
}
.tabText {
  color: white;
  font-size: 1rem;
  line-height: 29px;
  letter-spacing: 1px;
}
.highlighter {
  height: 80%;
  background: linear-gradient(
    180deg,
    RGBA(255, 255, 255, 0.0620629) 0%,
    RGBA(255, 255, 255, 0.0755026) 100%
  );
  box-shadow: 0px 0px 24px RGB(72 53 234 / 20%) inset;
  border-radius: 8px;
}
.wrapper {
  padding: 0 5px;
  box-sizing: border-box;
  height: 50px;
}
