@import "sass/index";
.root {
  margin-top: 50px;
  padding: 0 20px;
  width: 100%;
  font-weight: 500;

  display: flex;
  justify-content: space-between;
}
.dataWrapper {
  margin: 10px;
}
.title {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 500;
  color: #9f8dd0;
  line-height: 26px;
  letter-spacing: 1px;
}
.text {
  margin: 0;
}

.textWin {
  color: $win-green;
}
.textLose {
  color: $lose-red;
}
