@import "sass/index.scss";

.root {
  position: relative;
  margin-top: 25px;
  font-weight: 500;
}
.textBox {
  padding: 5px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 20px 5px;
}

.text {
}
.textTitle {
  margin: 0;
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: 1px;
  color: #9f8dd0;
}
.textContent {
  margin: 0;
  font-size: 0.85rem;
}
.profitWrapper {
  position: relative;
  display: flex;
  padding-left: 15px;
}

.win {
  color: $win-green;
}
.lose {
  color: $lose-red;
}

.buttonWrapper {
  width: 100%;
}
.refreshButton {
  width: 100%;
  max-width: 290px;
  height: 40px;
  margin: 10px auto 0;
  display: flex;

  border-radius: 8;
  background: linear-gradient(
    180deg,
    rgba(255, 255, 255, 0.0620629) 0%,
    rgba(255, 255, 255, 0.0755026) 100%
  ) !important;
  border: 1px dashed rgba(72, 53, 234, 0.6);

  &:hover {
    & svg {
      transform: scale(1.2) rotate(360deg);
    }
  }
}

.buttonLabel {
  display: flex;
  transition: transform 0.5s;
  transform: scale(0.9);
  width: min-content;
}
.refreshIcon {
  width: 24px;
  height: 24px;
  transition: transform 0.35s;
  & > path {
    fill: white;
  }
}
